.visible {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.3);
  position: fixed;
  top: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
}
.hidden {
  display: none;
}
.modalBlock {
  background-color: white;
  width: 60vw;
  min-height: 50vh;
  box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
  position: relative;
  padding: 20px;
}
.title {
  margin-top: 60px;
}
.closeBtn {
  position: absolute;
  top: 5%;
  right: 5%;
  background-color: #4e598c;
  border: none;
  color: white;
  cursor: pointer;
  padding: 5px;
}
.img {
  width: 20px;
}
.trash {
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.table {
  width: 100%;
  text-align: center;
  border-collapse: collapse;
  thead {
    color: #b0bcd2;
  }
  tr {
    display: flex;
    justify-content: space-between;
    td, th {
      padding: 5px 0;
      width: 100%;
    }
  }
}

@media screen and (max-width: 400px) {
  .table {
    thead {
      tr > th:nth-child(2) {
        display: none;
      }
    }
    tbody {
      tr > td:nth-child(2) {
        display: none;
      }
    }
  }
}