.button {
    background-color: #4e598c;
    border: none;
    color: white;
    cursor: pointer;
    padding: 5px;
    position: absolute;
    top: 5%;
    right: 0;
}
.container {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
}
.elementInfoBlock {
    position: relative;
}
.addToProfile {
    background-color: #4e598c;
  border-radius: 20px;
  border: none;
  width: 20px;
  height: 20px;
  display: flex;
  align-items: end;
  justify-content: flex-start;
  cursor: pointer;
  color: white;
  margin-left: 20px;
}
.title {
    display: flex;
    align-items: center;
}