.visible {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.3);
  position: fixed;
  top: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
}
.hidden {
  display: none;
}
.modalBlock {
  background-color: white;
  width: 60vw;
  height: 50vh;
  box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
  position: relative;
  padding: 20px;
}
.closeBtn {
  position: absolute;
  top: 5%;
  right: 5%;
  background-color: #4e598c;
  border: none;
  color: white;
  cursor: pointer;
  padding: 5px;
}
.input {
  width: 20%;
  margin-right: 20px;
}
.submit {
  background-color: #4e598c;
  border: none;
  color: white;
  cursor: pointer;
  padding: 5px;
}
.title {
  margin-top: 60px;
}
@media screen and (max-width: 630px) {
  .input {
    width: 70%;
  }
}
@media screen and (max-width: 440px) {
  .input {
    width: 100%;
  }
  .submit {
    margin-top: 20px;
  }
}